export let showRandomBgcStyle = () => {
    let x = Math.ceil(Math.random() * 10)

    function random(x) {
        if (x <= 3) {
            return style.coin
        }
        if (x <= 7) {
            return style.coin2
        }
        if (x <= 10) {
            return style.coin3
        }
    }

    let result = random(x)
    localStorage.setItem('showRandomBgcStyle', result)
}

